    }
    #_about-product .image .mold{
        position: absolute;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        filter: blur(15px);
    }
    
    #_about-product img {
        object-fit: contain;
