<template>
    <div class="user" id="right-info">
        <div class="group-about-user">
            <div class="about-user">
                <img class="avata" v-bind:src="product.avata ? api+'/image/avata/'+product.avata : '/tizi/img/avata-default.jpg'" />
                <div class="text">
                    <h3 class="title">{{ product.name_store }}</h3>
                    <span v-if="check_online(product.id_user)" class="status"><i class="fa fa-circle text-success"></i> Đang hoạt động</span>
                    <span v-else class="status"><i class="fa fa-circle text-success" style="color:red;"></i> Đã offline</span>
                </div>
                <div class="link">
                    <router-link :to="{name: 'route.store', params:{store: product.url_store}}">
                        <i class="fa fa-eye" aria-hidden="true"></i> Xem
                    </router-link>
                </div>
            </div>
            <div class="about-user-2">
                <div v-if="product.is_vip==1" class="store">
                    <h5>Chuyên</h5>
                    <i class="fa fa-archive" aria-hidden="true"></i>
                </div>
                <div v-else class="store">
                    <h5>Cá nhân</h5>
                    <i class="fa fa-user" aria-hidden="true"></i>
                </div>
                <div class="created-at-store">
                    <h5>Ngày tham gia</h5>
                    <span>{{ getTime(product.created_at).format("DD/MM/YYYY") }}</span>
                </div>
            </div>
        </div>
        <a class="phone" :href="`tel:${product.phone}`"><i class="fa fa-phone" aria-hidden="true"></i> 0{{ number_format(product.phone,0,"."," ") }}</a>
        <a v-on:click="chat(product.id_user, product.id)" class="chat"><i class="fa fa-comments" aria-hidden="true"></i> CHAT VỚI NGƯỜI BÁN</a>
        <div class="row other">
            <div class="col-lg-4 col-md-4">
                <img src="/tizi/img/3_mobile.png" alt="">
            </div>
            <div class="col-lg-8 col-md-8">
                <h5>NÊN gặp mặt trực tiếp kiểm tra hàng trước khi giao dịch.</h5>
                <a>Tìm hiểu thêm >></a>
            </div>
        </div>             
    </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from 'axios';
import moment from "moment";
moment.locale("vi");

export default {
    data(){
        return {
            api: this.$store.state.domainApi
        }
    },
    props:['product'],
    methods:{
        getTime(date){
            var time = new moment(date);
            return time;
        },
        chat(id_user, id_product){
            if(this.$store.state.token){
                if(this.$store.state.user.id == id_user) this.$swal("Đây là sản phẩm của bạn !","", "error");
                else this.openBox(id_user, id_product);
            }else this.$swal.fire({
                title: 'Vui lòng đăng nhập !',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Thoát',
                confirmButtonText: 'Đăng nhập'
            }).then((result) => {
                this.$store.commit('getRoute', this.$route);// lich trang truoc khi login
                if (result.value) this.$router.push({name: 'login'});
            });
        },
        openBox(id_user, id_product){
            var data={
                id_product:id_product,
                product_name: this.product.name,
                product_category: this.product.category,
                product_url: this.product.url,
                product_url_category: this.product.url_category,
                product_url_province: this.product.url_province,
                product_url_district: this.product.url_district,
                product_image: this.product.id_group_image,
                product_price: this.product.price,
                id_user: id_user,
                username: this.product.username,
                avata: this.product.avata
            };

            this.$store.state.boxMessage.data=data;
            axios.get(this.api+"/auth/message",{
                params:{
                    id_product:id_product,
                    id_user1: id_user,
                    id_user2: this.$store.state.user.id 
                }
            })
            .then(response=>{
                this.$store.state.boxMessage.content=response.data;
            });
            
            this.$store.state.boxMessage.isBoxMessage = true;
        },
        check_online(id_user){
            return this.users_online.find(user=>user.about.id==id_user) ? true : false;
        },
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        }
    },
    computed: {
        ...mapGetters(['socket','users_online'])
    }
}
</script>

