<template>
    <div class="col-lg-2 col-md-2 col-sm-2 col-xs-2">
        <a v-on:click="show" href="#" class="link" id="block-product"><i class="fa fa-times-circle-o" aria-hidden="true"></i> Báo xấu</a>
        <div :class="{ modal: true, fade: true, in: true ,show: menuLoc.status }">
            <div class="modal-dialog">
                <transition name="slide-fade">
                    <div v-if="menuLoc.content" class="modal-content">
                        <form @submit.prevent="submit">
                            <div class="modal-header">
                                <button type="button" class="close" v-on:click="hide">
                                <span aria-hidden="true">&times;</span></button>
                                <h4 class="modal-title">Báo xấu sản phẩm</h4>
                            </div>
                            <div class="modal-body">
                                <div class="box-header">
                                    <h3 class="box-title">Nội dung</h3>
                                    <span style="color:red; margin-left:20px;">{{ errors.first("content") }}</span>
                                </div>
                                <!-- /.box-header -->
                                <div class="box-body">
                                    <vue-editor v-model="content" name="content" v-validate="'required|min:5'" :editor-toolbar="customToolbar" />
                                </div>
                            </div>
                            <div class="modal-footer">
                                <button type="button" class="btn btn-default pull-left" v-on:click="hide">Thoát</button>
                                <button type="submit" class="btn btn-primary">Gửi</button>
                            </div>
                        </form>
                    </div>
                </transition>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { Validator } from 'vee-validate';
import { VueEditor } from "vue2-editor";
export default {
    components: {VueEditor},
    data(){
        return {
            api: this.$store.state.domainApi,
            menuLoc: {
                status: false,
                content: false
            },
            customToolbar: [
                ['bold', 'italic', 'underline'],
                [{ 'list': 'ordered'}, { 'list': 'bullet' }],
                [{ 'align': ''},{ 'align': 'justify'}, { 'align': 'right' }],
                ['link', 'video']
            ],
            content:'',
            id_user:1
        }
    },
    props:['id_product'],
    methods:{
        show () {
            if(this.$store.state.token){
                this.menuLoc.status = true;
                this.menuLoc.content = true;
                document.querySelector("body").classList.add('modal-open');
            }else  this.$swal.fire({
                title: 'Vui lòng đăng nhập !',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Thoát',
                confirmButtonText: 'Đăng nhập'
            }).then((result) => {
                if (result.value) this.$router.push({name: 'login'});
            });
        },
        hide () {
            this.menuLoc.content = false;
            document.querySelector("body").classList.remove('modal-open');
            setTimeout(() => {
                this.menuLoc.status = false;
            }, 400);
        },
        submit() {
            this.$validator.validate().then(valid => {
                if (valid) {
                    this.$swal.fire({
                        title: 'Đang gửi',
                        text: "Vui lòng chờ !",
                        allowOutsideClick: false
                    });
                    this.$swal.showLoading();
                    axios.post(`${this.api}/public/productfeedback`, {
                        id_product: this.id_product,
                        id_user: this.id_user,
                        content: this.content
                    })
                    .then(response => {
                        this.$swal.close();
                        if(response.data.success===1){
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.hide();
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                    });
                }
            });
        },
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');
    }
}
</script>

<style scoped>
@media(max-width: 767px) {
    #block-product.link{
        margin-top: 10px;
        font-size: 10px;
        color: white;
        background: #ff0000;
        padding: 6px 5px;
    }
}
@media(max-width: 400px){
    #block-product.link{
        margin-top: 10px;
    }
}
@media(max-width: 375px){
    #block-product.link{
        margin-top: -6px;
    }
}
